package io.github.dennistsar.sirs_kobweb.components.sections

import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.toCssColor
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.styleModifier
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.base
import com.varabyte.kobweb.silk.theme.toSilkPalette
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.Position
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.css.vh

val SideNavStyle = ComponentStyle.base("side-nav") {
    Modifier
        .position(Position.Sticky)
        .top(0.px)
        .height(100.vh)
        // Intentionally invert the header colors from the rest of the page
        .backgroundColor(colorMode.toSilkPalette().color)
        .color(colorMode.toSilkPalette().background)
        .zIndex(100)
        .transition("0.3s")
}

val MobileNavStyle = ComponentStyle.base("mobile-nav") {
    Modifier
        .position(Position.Fixed)
        .backgroundColor(colorMode.toSilkPalette().background)
        .border(1.px, LineStyle.Solid, colorMode.toSilkPalette().color.toCssColor())
        .left(0.px)
        .right(0.px)
        .bottom(0.px)
        .zIndex(1000)
        .margin(topBottom = 30.px, leftRight = 30.px)
        .transition("top .35s ease")
        .styleModifier { property("height", "fit-content") }
}