package io.github.dennistsar.sirs_kobweb.data

import Entry
import Ratings
import combine
import getRatingStats
import getTotalRatings
import roundToDecimal

fun List<Entry>.mapByCourses(): Map<String, List<Entry>> {
    // Currently, a not-ideal approach to try to only show profs and not TAs - probably should be a boolean or just marked somehow
    return groupBy { it.code.getCourseFromFullCode() }
        .mapValues { (_, v) ->
            v.filter { "Lecture" in it.courseName || it.note != null }
                .ifEmpty { v }
        }
}

fun Map<String, List<Entry>>.toProfScores(): Map<String,Ratings> {
    return filter { it.value.isNotEmpty() } // needed?
        .mapValues { (_, v) -> v.getTotalRatings().subList(0, 10) }
}

fun formatName(name: String): String {
    return name
        .replace(" \\(.*\\)|,".toRegex(), "") // removes stuff in parentheses & removes commas
        .split(" ")
        .let { parts ->
            parts[0] + (parts.getOrNull(1)?.let { ", ${it[0]}" } ?: "") // Adds first initial if present
        }.uppercase()
}

fun Ratings.toDisplayNums(): List<Number> {
    val statsByQ = getRatingStats()
    return statsByQ.map { it.first } + statsByQ.map { it.second }.average().toInt()
}

fun Map<String, List<Number>>.getAve(
    key: String = "Average",
    ignoreKey: String? = "Overall",
): Map<String, List<Number>>? {
    if (size == 1)
        return null
    val aves = filterKeys { it != ignoreKey }.values
    val ave = (0..9).map { i -> // corresponding to each question
        aves.map { it[i].toDouble() }.average().roundToDecimal(2)
    }
    return mapOf(key to ave.plus(0))
}

fun Map<String,Ratings>.addOverallElement(key: String = "Overall"): Map<String,Ratings> {
    if (size == 1) return this
    return plus(key to values.combine())
}

fun Map<String, List<Entry>>.toDisplayMap(): Map<String, List<Number>> {
    return toProfScores()
        .mapValues { it.value.toDisplayNums() }
}

fun String.getCourseFromFullCode(): String = split(':')[2]