package io.github.dennistsar.sirs_kobweb.misc

import com.varabyte.kobweb.compose.css.*
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.styleModifier
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.components.style.hover

val formLoadingGrid = ComponentStyle("form-loading-grid") {
    base {
        Modifier
            .gridTemplateColumns("repeat(1, 1fr)")
            .justifyItems(JustifyItems.Center)
    }
    Breakpoint.MD { Modifier.gridTemplateColumns("1fr auto 1fr") }
}

val underlineOnHoverStyle = ComponentStyle("underline-on-hover") {
    hover {
        Modifier
            .textDecorationLine(TextDecorationLine.Underline)
            .cursor(Cursor.Pointer)
    }
}

val centeredOnSm = ComponentStyle("centered-on-sm") {
    base { Modifier.justifySelf(JustifySelf.Center) }
    Breakpoint.MD { Modifier.justifySelf(JustifySelf.Inherit) }
}


fun Modifier.textEllipsis(): Modifier =
    overflow(Overflow.Hidden)
        .whiteSpace(WhiteSpace.NoWrap)
        .styleModifier { property("text-overflow", "ellipsis") }