@Page
@Composable
fun SearchDept() {
    //region non-UI setup
    val ctx = rememberPageContext()
    val coroutineScope = rememberCoroutineScope()
//    val colorMode by rememberColorMode()
    val originalOnPopState = remember { window.onpopstate } // need to use this to avoid infinite recursion
    val repository = remember { GithubSource() }

    var navOpenMobile by remember { mutableStateOf(false) }
    var mobileBackOpacity by remember { mutableStateOf(0.0) }
    var mobileNavPosition by remember { mutableStateOf(-1000) }

    val viewModel = remember {
        SearchDeptViewModel(
            repository = repository,
            coroutineScope = coroutineScope,
            initialSchool = ctx.params["school"],
            initialDept = ctx.params["dept"],
            initialCourse = ctx.params["course"],
            initialProf = ctx.params["prof"]?.decodeURLParam()?.uppercase(),
        )
    }

    DisposableEffect(true) {
        window.onpopstate = {
            originalOnPopState?.run { invoke(it) } // keeps default behavior when going back to other page
            viewModel.onPopState(window.location.search)
        }
        onDispose {
            window.onpopstate = originalOnPopState
        }
    }

    val status = viewModel.status

    remember(viewModel.url) {
        if (status != Status.InitialLoading)
            ctx.router.routeTo(viewModel.url)
    }
    // endregion

    Row(
        Modifier
            .fillMaxSize()
            .flexWrap(FlexWrap.Nowrap)
    ) {
        Box(
            SideNavStyle.toModifier()
                .displayIf(Breakpoint.MD)
                .flex(0, 0, 325.px)
        ) {
            Column(
                Modifier.height(100.vh).fillMaxWidth(),
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                if (viewModel.status != Status.InitialLoading)
                    SearchDeptFormContent(viewModel)

                Spacer()
                Footer()
            }
        }


        if (navOpenMobile) {
            TransitionObject(
                startTransition = {
                    mobileBackOpacity = .5
                    mobileNavPosition = 0
                },
                onEndTransition = { if (mobileBackOpacity == 0.0) navOpenMobile = false }
            ) {
                Box(
                    MobileNavStyle.toModifier()
                        .top(mobileNavPosition.px)
                        .then(it),
                    contentAlignment = Alignment.Center
                ) {
                    if (viewModel.status != Status.InitialLoading) {
                        SearchDeptFormContent(viewModel)
                    }
                }
                DarkBackground(
                    Modifier
                        .opacity(mobileBackOpacity)
                        .onClick {
                            mobileBackOpacity = 0.0
                            mobileNavPosition = -1000
                        }.then(it)
                )
            }
        }

        MobileNavButton {
            navOpenMobile = true
            mobileBackOpacity = 0.0
            mobileNavPosition = -1000
        }

        Column(
            modifier = Modifier
                .textAlign(TextAlign.Center)
                .height(100.percent)
                .padding(1.cssRem)
                .boxShadow(
                    offsetX = 10.px,
                    offsetY = 5.px,
                    blurRadius = 30.px,
                    spreadRadius = 0.px,
                    color = Color.rgba(0,0,0,.1f),
                    inset = true,
                ).flexGrow(1),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Row(
                Modifier
                    .flexShrink(0)
                    .height(75.px) // to match height of LoadingSpinner
                    .alignSelf(AlignSelf.Start),
                verticalAlignment = Alignment.CenterVertically,
            ) {
                SpanText(
                    "Search Result",
                    Modifier
                        .fontSize(2.cssRem)
                        .fontWeight(FontWeight.Bold)
                )
                if (viewModel.status == Status.InitialLoading || viewModel.profListLoading)
                    LoadingSpinner()
            }

            val finishLoading = { viewModel.profListLoading = false }
            if (status == Status.Prof) {
                SpanText(viewModel.state.profState.selected)
                ProfSummary(
                    viewModel.selectedProfEntries,
                    viewModel.applicableCourseAves,
                    { viewModel.onSelectCourse(it) },
                    finishLoading,
                )
            }
            viewModel.mapToDisplay?.run {
                key(size / keyReset) {
                    ProfScoresList(
                        this,
                        viewModel.stickyDisplayMap ?: emptyMap(),
                        viewModel.instructors,
                        { viewModel.onSelectProf(it) },
                        finishLoading,
                    )
                }
            }
        }
    }
}

@Composable
fun ProfSummary(
    entries: List<Entry>,
    applicableCourseAves: Map<String, List<Double>>,
    onNameClick: (String) -> Unit = {},
    onLoad: () -> Unit = {},
) {
    val a = entries.mapByCourses()
    val b = a.toProfScores().addOverallElement()
    val allScores = entries.getTotalRatings()

    // this will obviously have to be moved to state object eventually
    var selectedQ by remember { mutableStateOf(8) }
    CustomDropDown(
        list = TenQsShortened,
        onSelect = { selectedQ = TenQsShortened.indexOf(it) },
        selected = TenQsShortened[selectedQ]
    )

    Row(
        Modifier
            .margin(topBottom = 30.px)
            .justifyContent(JustifyContent.Center),
        verticalAlignment = Alignment.CenterVertically,
    ) {// for future reference: "?school=01&dept=640&prof=TUNNELL%2C%20J" has a lot of graphs
        b.entries
            .sortedBy { it.key }
            .forEach { (courseName, scores) ->
                val ratings = scores[selectedQ]

                Column(
                    Modifier.margin(topBottom = 10.px, leftRight = 15.px),
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    val selectedScore = ratings.getRatingStats().first
                    val courseAve = applicableCourseAves[courseName]?.get(selectedQ)

                    SpanText(courseName)
                    BarGraph(
                        ratings,
                        if (selectedQ < 8) Pair("Strongly Disagree", "Strongly Agree")
                        else Pair("Poor", "Excellent")
                    )
                    SpanText(
                        "Prof Average",
                        courseAve?.let {
                            Modifier.color(if (selectedScore >= it) Colors.Green else Colors.Red)
                        } ?: Modifier
                    )
                    Row(verticalAlignment = Alignment.CenterVertically) {
                        StarRating(selectedScore)
                        SpanText(selectedScore.toString(), Modifier.margin(left = 5.px))
                    }
                    courseAve?.let {
                        SpanText("Course Average")
                        Row(verticalAlignment = Alignment.CenterVertically) {
                            StarRating(it)
                            SpanText(it.toString(), Modifier.margin(left = 5.px))
                        }
                    }
                }
            }
    }

    val temp = b.mapValues { it.value.toDisplayNums() }
    ProfScoresList(
        temp,
        temp.getAve() ?: emptyMap(),
        null,
        onNameClick,
        onLoad,
    )
}

@Composable
fun SearchDeptFormContent(viewModel: SearchDeptViewModel) {
    val modifier2 = Modifier//.fillMaxSize()
//            .backgroundColor(Color.chocolate)
    val modifier1 = Modifier//.width(7.8.cssRem)//.backgroundColor(Color.palevioletred)
    val labelModifier = Modifier.fontWeight(FontWeight.Bold).fontSize(1.1.cssRem)

    Column(
        horizontalAlignment = Alignment.CenterHorizontally,
    ) {

        Column(
            Modifier
                .rowGap(.5.cssRem)
                .margin(topBottom = .5.cssRem),
            horizontalAlignment = Alignment.CenterHorizontally,
        ) {

            SpanText("School", labelModifier)
            CustomDropDown(
                list = viewModel.state.schoolState.list,
                onSelect = { viewModel.onSelectSchool(it) },
                selectModifier = modifier1.fontSize(0.75.cssRem),
                optionModifier = modifier2.fontSize(0.75.cssRem),
                getText = { "${it.code} - ${it.name}" },
                getValue = { it.code },
                selected = viewModel.state.schoolState.selected,
            )

            Column(
                horizontalAlignment = Alignment.CenterHorizontally,
            ) {
                SpanText("Department", labelModifier)
                CustomDropDown(
                    list = viewModel.state.deptState.list,
                    onSelect = { viewModel.onSelectDept(it) },
                    selectModifier = modifier1,
                    optionModifier = modifier2,
                    getText =
                    {
                        //"${it.first} - ${it.second}"
                        it.second.run {
                            if (length > 32)
                                substring(0, 30) + "..."
                            else this
                        }
                    },
                    getValue = { it.first },
                    selected = viewModel.state.deptState.selected,
                )
            }
            Column(
                horizontalAlignment = Alignment.CenterHorizontally,
            ) {
                SpanText("Course (Optional)", labelModifier)
                key(viewModel.state.courseState.list.size / keyReset) {
                    CustomDropDown(
                        list = viewModel.state.courseState.list,
                        onSelect = { viewModel.onSelectCourse(it) },
                        selectModifier = modifier1,
                        optionModifier = modifier2,
                        selected = viewModel.state.courseState.selected,
                    )
                }
            }
            Column(
                horizontalAlignment = Alignment.CenterHorizontally,
            ) {
                SpanText("Prof (Optional)", labelModifier)
                key(viewModel.state.profState.list.size / keyReset) {
                    CustomDropDown(
                        list = viewModel.state.profState.list,
                        onSelect = { viewModel.onSelectProf(it) },
                        selectModifier = modifier1,
                        optionModifier = modifier2,
                        selected = viewModel.state.profState.selected,
                    )
                }
            }
        }
    }
}