import org.jetbrains.compose.web.attributes.disabled
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.CheckboxInput
import org.jetbrains.compose.web.dom.Text
import com.varabyte.kobweb.compose.ui.graphics.Color as KobColor

private val firstColWidth = 2.cssRem
private val secondColWidth = 9.cssRem
private val secondColWidthMobile = 6.5.cssRem
private val mainColWidth = 4.5.cssRem

//private val firstRowHeight = 9.5.cssRem
private val mainRowHeight = 2.cssRem

private val fontSize = .9.cssRem

val profScoresGridVariant = SimpleGridStyle.addVariant("prof-scores") {
    base {
        Modifier.gridTemplateColumns("$firstColWidth $secondColWidthMobile repeat(2, $mainColWidth)")
    }
    Breakpoint.MD {
        Modifier.gridTemplateColumns("$firstColWidth $secondColWidth repeat(11, $mainColWidth)")
    }
}

val mainGridArea = ComponentStyle("main-grid-area") {
    base { Modifier }
    Breakpoint.MD { Modifier.padding(leftRight = 3.cssRem) }
}

@OptIn(ExperimentalComposeWebApi::class)
@Composable
fun ProfScoresList(
    list: Map<String, List<Number>>,
    stickyList: Map<String, List<Number>> = emptyMap(),
    instructors: List<Pair<String, Boolean>>? = null,
    onNameClick: (String) -> Unit = {},
    onLoad: () -> Unit = {},
) {
    var selectedQ by remember { mutableStateOf(8) }
    var selectedQDropDown by remember { mutableStateOf(selectedQ) } // same as selectedQ but w/o # of Responses

    // region mobile question selector
    Column(
        Modifier
            .justifyContent(JustifyContent.Center)
            .width(50.vw)
            .margin(bottom = .5.cssRem)
            .displayUntil(Breakpoint.MD),
        horizontalAlignment = Alignment.CenterHorizontally,
    ) {
        // The SpanText modifier and 7.8.cssRem are from SearchDeptFormContent() - update to use vars
        SpanText("Question", Modifier.fontWeight(FontWeight.Bold).fontSize(1.1.cssRem))
        CustomDropDown(
            list = TenQsShortened,
            selectModifier = Modifier.maxWidth(100.percent).minWidth(7.8.cssRem),
            onSelect =
            {
                selectedQ = TenQsShortened.indexOf(it)
                selectedQDropDown = selectedQ
            },
            selected = TenQsShortened[selectedQDropDown]
        )
    }
    // endregion

    var showOnlyTeaching by mutableStateOf(false)
    val instructorNamesOnly = instructors?.map { (name, _) -> name } ?: emptyList()
    val instructorsInList = list.keys.filter { it in instructorNamesOnly }
    instructors?.let {
        val applicable = instructorsInList.isNotEmpty() //&& applicableInstructors.size != list.size
        Column(
            horizontalAlignment = Alignment.CenterHorizontally,
        ) {
            Row(
                Modifier
                    .cursor(if (applicable) Cursor.Pointer else Cursor.Default)
                    .onClick { if (applicable) showOnlyTeaching = !showOnlyTeaching },
                verticalAlignment = Alignment.CenterVertically,
            ) {
                CheckboxInput(showOnlyTeaching) { if (!applicable) disabled() }
                SpanText(
                    "Fall 2022 instructors only",
                    Modifier.userSelect(UserSelect.None).thenIf(!applicable, Modifier.opacity(0.4))
                )
            }
            instructors
                .filter { it.second && it.first !in instructorsInList }
                .takeIf { it.isNotEmpty() }
                ?.let {
                    val displayStr =
                        if (applicable)
                            "Also teaching Fall 2022 but not in list: "
                        else
                            "Teaching Fall 2022 but not in list: "
                    SpanText(displayStr)
                    it.forEach { (name, bool) ->
                        SpanText(
                            name,
                            Modifier.thenIf(bool, underlineOnHoverStyle.toModifier().onClick { onNameClick(name) })
                        )
                    }
                }
        }
    }

    val palette = SilkTheme.palettes[getColorMode()]

    Column(mainGridArea.toModifier()) {
        CustomGrid(
            Modifier.gridAutoRows(134.4.px.toString()), // this comes from somewhere but idk where
            profScoresGridVariant
        ) {
            (listOf("", "") + TenQsShortened + "Total # of Responses")
                .forEachIndexed { index, text ->
                    SpanText(
                        text,
                        Modifier
                            .zIndex(99 - index)
                            .width(11.cssRem) // wider than Box so that text extends longer
                            .margin(topBottom = 3.cssRem, leftRight = (-1).cssRem)
                            .fontSize(fontSize)
                            .textDecorationLine(TextDecorationLine.Underline)
                            .transform { rotate((-45).deg) }
                            .thenIf(index - 2 == selectedQ, Modifier.fontWeight(FontWeight.Bold))
                            .thenIf(index != 0,
                                Modifier
                                    .cursor(Cursor.Pointer)
                                    .onClick {
                                        selectedQ = index - 2
                                        if (selectedQ != 10)
                                            selectedQDropDown = selectedQ
                                    }
                            ).thenIf(
                                index !in listOf(0, 1, selectedQDropDown + 2, 12),
                                Modifier.displayIf(Breakpoint.MD)
                            ).thenIf(index - 2 in TenQs.indices) { Modifier.title(TenQs[index - 2]) }
                    )
                }
        }


        CustomGrid(
            Modifier
                .gridAutoRows(mainRowHeight.toString())
//                .margin(bottom = 4.cssRem)// temp?
                .maxHeight(55.vh)// temp?
                .borderTop(5.px, LineStyle.Solid, palette.button.default.toCssColor())
                .overflowY(Overflow.Auto),
            profScoresGridVariant
        ) {
            val gridModifier = Modifier
                .fillMaxSize()
                .fontSize(fontSize)

            val stickyModifier = gridModifier
                .position(Position.Sticky)
                .top(0.px)
                .bottom(0.px)
                .backgroundColor(palette.background.shifted())
                .fontWeight(FontWeight.Bold)

            // probably should revert these to being in same list
            (list.entries + stickyList.entries).run {
                if (showOnlyTeaching)
                    filter { it.key in instructorsInList }
                else this
            }.sortedWith(
                compareBy(
                    { -it.value[selectedQ].toDouble() },
                    { -it.value.last().toDouble() },
                )
            ).forEachIndexed { i, (prof, nums) ->
                val rowColorModifier = gridModifier.then(
                    when {
                        prof == "Average" -> stickyModifier
                        i % 2 == 0 -> Modifier.backgroundColor(KobColor.rgb(0xDCE9FA))
                        else -> Modifier
                    }
                )

                Box(
                    rowColorModifier.fontWeight(FontWeight.Bold),
                    contentAlignment = Alignment.Center,
                ) { Text((i + 1).toString()) }

                Box(
                    rowColorModifier,
                    contentAlignment = Alignment.CenterStart,
                ) {
                    SpanText(
                        prof,
                        underlineOnHoverStyle.toModifier()
                            .maxWidth(100.percent)
                            .textEllipsis()
                            .role("button") // prevents mobile text highlight on click
                            .onClick { onNameClick(prof) }
                            .thenIf(prof.length > 15, Modifier.title(prof))
                            .thenIf(prof in instructorsInList, Modifier.color(palette.button.default))
                    )
                }

                nums.forEachIndexed { index, num ->
                    Box(
                        rowColorModifier
                            .thenIf(
                                index !in listOf(selectedQDropDown, 10),
                                Modifier.displayIf(Breakpoint.MD)
                            ),
                        contentAlignment = Alignment.Center,
                    ) { Text(num.toString()) }
                }
            }
        }
    }
    remember(list) { onLoad() } // since loading only happens when list is changed
}