package io.github.dennistsar.sirs_kobweb.components.widgets

import androidx.compose.runtime.Composable
import androidx.compose.runtime.DisposableEffect
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.asAttributesBuilder
import com.varabyte.kobweb.compose.ui.graphics.Color
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.styleModifier
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.theme.SilkTheme
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.css.vmin
import org.jetbrains.compose.web.dom.Option
import org.jetbrains.compose.web.dom.Select
import org.jetbrains.compose.web.dom.Text
import org.w3c.dom.HTMLOptionElement
import org.w3c.dom.get

val SelectStyle = ComponentStyle("select-mine") {
    val palette = SilkTheme.palettes[colorMode]

    val selectColor =
        if (colorMode.isDark())
            Color.rgb(0x647890).darkened()//palette.color.lightened(.2f)
        else
            Color.rgb(0xDCE9FA)//palette.background.darkened(.1f)

    base {
        Modifier
            .fontFamily("Segoe UI")
            .fontWeight(FontWeight.Bolder)
            .color(palette.color)
            .styleModifier { property("font-variant", "small-caps") }
            .borderRadius(1.vmin)
            .padding(4.px)
            .backgroundColor(selectColor)
            .border("none")
    }
}

@Composable
fun <T> CustomDropDown(
    list: Collection<T>,
    onSelect: (String) -> Unit,
    selectModifier: Modifier = Modifier,
    optionModifier: Modifier = Modifier,
    getText: (T) -> String = { it.toString() },
    getValue: (T) -> String = getText,
    selected: String?,
) {
    Select(
        SelectStyle.toModifier()
            .then(selectModifier)
            .asAttributesBuilder {
                onChange {
                    onSelect(it.value ?: "Error")
                }
            }
    ) {
        DisposableEffect(selected) {
            val index = list.map(getValue).indexOf(selected).takeIf { it >= 0 } ?: 0
            (scopeElement.options[index] as? HTMLOptionElement)?.selected = true
            onDispose {}
        }
        list.forEach {
            Option(
                getValue(it),
                optionModifier.asAttributesBuilder(),
            ) {
                Text(getText(it))
            }
        }
    }
}